.home_wrap {
    height: calc(100% - 27%);

    // overflow-x: hidden;
    overflow: hidden;
    overflow-y: scroll;
    padding-bottom: 4rem;
    box-sizing: border-box;

    .tab {
        height: 2.5rem;
        background-color: #fff;
        box-shadow: 0 1px 8px rgb(233, 233, 233);
        position: sticky;
        top: 0;
        z-index: 999;

        ul {
            height: 100%;
            display: flex;
            overflow-x: scroll;
            overflow-y: hidden;

            li {
                width: auto;
                transition: 300ms;
                flex-grow: 0;
                flex-shrink: 0;
                margin-right: 1rem;
                line-height: 2.4rem;

                &:nth-of-type(1) {
                    margin-left: 1rem;
                }
            }
        }
    }

    .food_wrap {
        box-sizing: border-box;
        width: 100%;
        height: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-top: 1rem;
        margin-left: 1rem;
        background-color: #fff;
        overflow: hidden;

        .food {
            width: 44.2%;
            height: 100%;
            display: flex;
            // border: solid 1px #000;
            overflow: hidden;
            border-radius: 0.6rem;
            margin-right: 1rem;
            margin-bottom: 0.9rem;
            box-shadow: 0px 0px 10px 0px rgba(9, 44, 76, 0.1);

            .food_item {
                width: 100%;
                height: 11rem;

                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .img_wrap {
                    height: 60%;
                    overflow: hidden;
                    position: relative;

                    img {
                        width: 100%;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }

                p {
                    text-indent: 0.6rem;
                }

                .price {
                    // text-indent: 0.6rem;
                    margin-bottom: 0.5rem;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    .new {
                        font-weight: 700;
                        font-size: 1rem;
                        margin-right: 0.2rem;
                    }

                    .old {
                        text-decoration: line-through;
                        font-size: 0.6rem;
                    }

                    i {
                        font-style: initial;
                    }
                }
            }
        }
    }
}